import { useRequestToken } from "./useRequestToken";
import {
  SubordinateDataProps,
  DataInfoVacationProps,
} from "../controlers/services/workflowData";
import { dateEnd } from "../util/vacationFunctions/vacationFunctions";
import { convertStringDate } from "../util/historyInfoVacationFunction/historyInfoVacationFunction";

export function useForm() {
  const { UserRequestDataToken, selectContributor } = useRequestToken();

  async function DataUserSolicitation(form) {
    const dataUserJson = await UserRequestDataToken();

    if (dataUserJson) {
      console.log(dataUserJson);
      form.setFieldsValue({
        fullname: dataUserJson.nomSol,
        registry: dataUserJson.cadSol,
        subsidiary: dataUserJson.filSol,
        office: dataUserJson.carSol,
        boss: dataUserJson.gesSol,
      });

      //console.log(dataUserJson);

      return dataUserJson;
    } else {
      return;
    }
  }

  async function subordinateData({ setNameSubordinate }) {
    const subordinateJson = await UserRequestDataToken();

    const subordinateData: SubordinateDataProps[] =
      await subordinateJson.busCol.map((name) => {
        //console.log(name);
        //console.log(name.nomFun);
        return {
          nomfun: name.nomFun,
          numemp: name.numEmp,
          tipcol: name.tipCol,
          numcad: name.numCad,
        };
      });

    setNameSubordinate(subordinateData);
    //console.log(subordinateJson.busCol);
    //console.log(subordinateData);
  }

  async function fillFormEmployee(form, dataEmployee: any) {
    //console.log(dataEmployee);

    const { filCol, gesCol, numCad, carCol } = await dataEmployee;
    //console.log(cargo);

    form.setFieldsValue({
      registryEmployee: numCad,
      positionEmployee: carCol,
      subsidiaryEmployee: filCol,
      bossEmployee: gesCol,
    });
  }

  async function historyVacationTable(
    vacationIdentity,
    setArrayTable,
    options?
  ) {
    let arrayVacation: any;
    let arrayInfo: DataInfoVacationProps[];

    if (vacationIdentity === "Requester") {
      arrayVacation = await UserRequestDataToken();
    } else {
      arrayVacation = await selectContributor(options);
    }

    //console.log(arrayVacation);
    //console.log(arrayVacation.colFer.perFer.length);

    if (arrayVacation && arrayVacation.colFer.perFer.length === undefined) {
      const item = arrayVacation.colFer.perFer;

      if (item.ferPer) {
        const stringToDate = convertStringDate(item.ferPer.iniFer);
        const dateEndVacation = dateEnd(stringToDate, item.ferPer.diaFer);
        //console.log(dateEndVacation);
        arrayInfo = [
          {
            key: "0",
            status: item.desSit,
            perAqIn: item.iniPer,
            perAqEnd: item.fimPer,
            dateIn: item.ferPer.iniFer,
            dateEnd: dateEndVacation,
            day: item.qtdDir,
            fault: item.qtdFal,
            salary13: item.ferPer.perFer.opc13s,
            allowence: item.qtdAbo,
            part: item.ferPer.perFer.qtdPAr,
          },
        ];
      } else {
        arrayInfo = [
          {
            key: "0",
            status: item.desSit,
            perAqIn: item.iniPer,
            perAqEnd: item.fimPer,
            dateIn: "",
            dateEnd: "",
            day: item.qtdDir,
            fault: item.qtdFal,
            salary13: "",
            allowence: item.qtdAbo,
            part: "",
          },
        ];
      }
    } else if (arrayVacation) {
      arrayInfo = await arrayVacation.colFer.perFer.map((item, index) => {
        if (item.ferPer) {
          const stringToDate = convertStringDate(item.ferPer.iniFer);
          const dateEndVacation = dateEnd(stringToDate, item.ferPer.diaFer);
          //console.log(item.ferPer.iniFer);
          //console.log(item.ferPer.diaFer);
          //console.log(dateEndVacation);
          return {
            key: index,
            status: item.desSit,
            perAqIn: item.iniPer,
            perAqEnd: item.fimPer,
            dateIn: item.ferPer.iniFer,
            dateEnd: dateEndVacation,
            day: item.qtdDir,
            fault: item.qtdFal,
            salary13: item.ferPer.opc13s,
            allowence: item.qtdAbo,
            part: item.ferPer.qtdPAr,
          };
        } else {
          return {
            key: index,
            status: item.desSit,
            perAqIn: item.iniPer,
            perAqEnd: item.fimPer,
            dateIn: "",
            dateEnd: "",
            day: item.qtdDir,
            fault: item.qtdFal,
            salary13: "",
            allowence: item.qtdAbo,
            part: "",
          };
        }
      });
    }

    //console.log(arrayInfo);
    setArrayTable(arrayInfo);
  }

  return {
    DataUserSolicitation,
    subordinateData,
    fillFormEmployee,
    historyVacationTable,
  };
}
