import { Input, Form, Row, Col, Select, Tooltip } from "antd";
import "./container.css";
import { useEffect, useState } from "react";
import { FieldData } from "../../../util/containerInterface";
import {
  SubordinateDataProps,
  //selectContributor,
  //fillFormEmployee,
  //historyVacationTable,
} from "../../../controlers/services/workflowData";
import { useRequestToken } from "../../../hooks/useRequestToken";
//import ThemeContext from "../../../contexts/ThemeContext";
import { useForm } from "../../../hooks/useForm";

export default function ContainerEmployee({ name, set }) {
  const [fields, setFields] = useState<FieldData[]>([
    { name: ["nameEmployee"], value: "" },
    { name: ["registryEmployee"], value: "" },
    { name: ["positionEmployee"], value: "" },
    { name: ["subsidiaryEmployee"], value: "" },
    { name: ["bossEmployee"], value: "" },
    { name: ["custCenter"], value: "" },
  ]);

  const [form] = Form.useForm();
  const { selectContributor } = useRequestToken();
  const names: SubordinateDataProps[] = name;
  const { fillFormEmployee, historyVacationTable } = useForm();
  const { Option } = Select;

  useEffect(() => {
    form.resetFields();
  }, []);

  //console.log(fields);

  return (
    <>
      <fieldset>
        <legend>Informações do Colaborador</legend>

        <Form
          name="formEmployee"
          labelCol={{ span: 10 }}
          wrapperCol={{ span: 12 }}
          labelAlign="left"
          fields={fields}
          form={form}
          onFieldsChange={(_, allFields) => {
            //setFields(allFields);
            //console.log(fields);
          }}
        >
          <Row gutter={20}>
            <Col span={12}>
              <Form.Item
                name={["nameEmployee"]}
                label={"Colaborador"}
                rules={[
                  { required: true },
                  { message: "Obrigatório selecionar o colaborador." },
                ]}
              >
                <Tooltip title="Selecione o nome do colaborador">
                  <Select
                    allowClear
                    showSearch
                    placeholder="Selecione Colaborador"
                    optionFilterProp="children"
                    onSelect={(label, options) => {
                      console.log(options);
                      fillFormEmployee(form, selectContributor(options));
                      historyVacationTable("Employee", set, options);
                    }}
                    filterOption={(input, option) =>
                      (option?.children as unknown as string)
                        .toLowerCase()
                        .indexOf(input.toLocaleLowerCase()) >= 0
                    }
                    filterSort={(optionA, optionB) =>
                      (optionA?.children as unknown as string)
                        .toLowerCase()
                        .localeCompare(
                          (optionB?.children as unknown as string).toLowerCase()
                        )
                    }
                  >
                    {names.map((name) => {
                      //console.log(value, index);
                      //console.log(name);
                      return (
                        <Option
                          key={
                            name.numemp + "_" + name.tipcol + "_" + name.numcad
                          }
                          value={name.nomfun}
                        >
                          {name.nomfun + " " + name.numcad}
                        </Option>
                      );
                    })}
                  </Select>
                </Tooltip>
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item name={["registryEmployee"]} label={"Matrícula"}>
                <Input readOnly />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={20}>
            <Col span={12}>
              <Form.Item
                name={["positionEmployee"]}
                label={"Cargo Colaborador"}
              >
                <Input readOnly />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item name={["subsidiaryEmployee"]} label={"Filial"}>
                <Input readOnly />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={20}>
            <Col span={12}>
              <Form.Item name={["bossEmployee"]} label={"Superior Imediato"}>
                <Input readOnly />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item></Form.Item>
            </Col>
          </Row>
        </Form>
      </fieldset>
    </>
  );
}
