import { QueryRequest } from "../controlers/services/queryRequest";
//import { useDataPlataform } from "./useDataPlataform";
import { useContext } from "react";
import ThemeContext from "../contexts/ThemeContext";

export function useRequestToken() {
  //const { email, token } = useDataPlataform();
  const { email, token } = useContext(ThemeContext);

  async function sendRequestToken(reqType, service, port, params?) {
    const mountUrl: QueryRequest = new QueryRequest();

    const tokenUser = await token;
    //const tokenUser = "TKbuov1E8WtcPFIDmOxFFWIKxxGaqxbY";

    if (tokenUser) {
      console.log("token: " + tokenUser);

      mountUrl.setService(service);
      mountUrl.setPort(port);
      mountUrl.setTypeReq(2);

      let headers = new Headers();

      headers.append("Accept", "application/json;charset=utf-8");
      //headers.append("Authorization", "Bearer " + process.env.REACT_APP_TOKEN);
      headers.append("Authorization", "Bearer " + tokenUser);

      console.log(mountUrl.createUrlRest());

      const response = await fetch(mountUrl.createUrlRest(), {
        mode: "cors",
        method: reqType,
        headers: headers,
        body: JSON.stringify(params),
      });

      return response;
    } else {
      console.log("Nenhum token");
      return;
    }
  }

  async function UserRequestDataToken() {
    console.log(email);
    //console.log(typeof email);
    const emaUsu = "JESSE.GUIMARAES@REXTURADVANCE.COM.BR";
    if (email) {
      const paramName = {
        //emaUsu: email,
        emaUsu,
      };

      //console.log(paramName);
      const dataUserRequest = await sendRequestToken(
        "POST",
        process.env.REACT_APP_SERVICE,
        process.env.REACT_APP_PORT_IDENTIFY,
        paramName
      );

      const responseJson = await dataUserRequest.json();

      //console.log(dataUserRequest);
      //console.log(responseJson);
      //historyVacationTable(responseJson.colFer.perFer);

      return responseJson;
    } else {
      //console.log("Não recebeu email");
      return;
    }
  }

  async function selectContributor(options) {
    //console.log(label);
    //console.log(options);
    //console.log(options.key);
    const arrkey = options.key.split("_");
    //console.log(arrkey);

    const parameters = {
      numEmp: parseInt(arrkey[0]),
      tipCol: parseInt(arrkey[1]),
      numCad: parseInt(arrkey[2]),
    };

    const contributorDataRequest = await sendRequestToken(
      "POST",
      process.env.REACT_APP_SERVICE,
      process.env.REACT_APP_PORT_EMPLOYEE,
      parameters
    );

    const contributorDataRequestJson = await contributorDataRequest.json();

    //historyVacationTable(contributorDataRequestJson.colFer.perFer);

    console.log(contributorDataRequestJson);
    //console.log(parameters);

    return contributorDataRequestJson;
  }

  return {
    useRequestToken,
    UserRequestDataToken,
    selectContributor,
  };
}
